  justify-content: space-between;
}
@media (max-width: 767px) {
  .contacts__wrapper {
    display: flex;
    flex-direction: column;
  }
}

.contacts__wrapper dl {
  font-size: 14px;
  line-height: 21px;
}
@media (max-width: 767px) {
  .contacts__wrapper dl {
    font-size: 18px;
    line-height: 27px;
