  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
}
@media (max-width: 767px) {
  .contacts__wrapper dt {
    font-size: 20px;
    line-height: 30px;
  }
}

.contacts__wrapper dd {
  margin: 0 0 15px;
}

.contacts__address {
  max-width: 170px;
  position: relative;
}
.contacts__address::before {
  content: "";
  position: absolute;
  top: 21px;
  right: -62px;
  width: 1px;
  height: 102px;
  background-color: #74819c;
}
@media (max-width: 767px) {
  .contacts__address {
    min-width: none;
