.feedback {
  padding-top: 56px;
  padding-bottom: 48px;
  padding-right: 169px;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .feedback {
    padding: 56px 133px;
    background-color: #102654;
  }
}
@media (max-width: 767px) {
  .feedback {
    padding: 70px 15px;
  }
}

.feedback__heading {
  margin: 0 0 10px;
  padding: 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 800;
  font-size: 54px;
  line-height: 84px;
  letter-spacing: 0.03em;
}
@media (max-width: 1199px) {
  .feedback__heading {
    font-weight: 700;
    font-size: 42px;
    line-height: 50px;
  }
}
@media (max-width: 767px) {
  .feedback__heading {
    font-weight: 600;
    font-size: 32px;
    line-height: 38px;
  }
}

.feedback__form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: min-content min-content;
  column-gap: 30px;
  row-gap: 28px;
  max-width: 414px;
}

.feedback__input input {
  width: 100%;
  padding: 10px 0 7px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #74819c;
}
.feedback__input input::placeholder {
  font-size: 14px;
  line-height: 16px;
}

.feedback__button {
  grid-column: 1/-1;
  grid-row: 2/-1;
  padding: 15px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  display: flex;
  align-items: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
@media (hover: hover) {
  .feedback__button:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.feedback__button.focus-visible:focus {
  opacity: 0.6;
}

.footer {
  padding-top: 43px;
  padding-bottom: 40px;
  color: #ffffff;
  background-color: #030a1a;
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 767px) {
