  margin-top: 2px;
  margin-right: 237px;
}

.footer__logo svg {
  width: 88px;
  height: 32px;
}
@media (max-width: 1199px) {
  .footer__logo svg {
    width: 82px;
    height: 18px;
  }
}
@media (max-width: 767px) {
  .footer__logo svg {
    width: 68px;
    height: 15px;
  }
}

.nav__list {
  display: grid;
  grid-template-columns: repeat(6, min-content);
