  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .nav__list {
    display: block;
    columns: 2;
  }
}

.nav__link {
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
}

.social {
  position: relative;
  top: -5px;
